const infoButton = document.querySelector('[data-button="info"]');

const changeText = () => {
  if (window.innerWidth >= 768) {
    infoButton.innerHTML = '  ';
  } else {
    infoButton.innerHTML = ' ';
  }
};

const changeOnResize = () => {
  window.addEventListener('resize', changeText);
};

export {changeText, changeOnResize};
